/* HEADER */
/* HEADER */
/* HEADER */

.header {
  position: fixed;
  top: 0;
  z-index: 1;
  width: 100%;
}

h1 {
  color: rgb(255, 255, 255);
  text-transform: uppercase;
  font-size: 18px;
}

.container {
  margin: 0;
  padding: 15px;
}

.navbar {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  background-color: black;
  padding: 15px;
}

.burgerLogo {
  width: 20px;
}

li {
  color: white;
  text-transform: uppercase;
}

.navbar a {
  text-decoration: none;
  color: #fff;
}

.logo {
  margin-right: 50px;
}

.list {
  list-style: none;
  display: flex;
  gap: 25px;
}

#toggler, .navbar label {
  display: none;
}

@media screen and (max-width: 600px) {
  .menu {
      width: 100%;
      max-height: 0;
      overflow: hidden;
  }

  .list {
      flex-direction: column;
      align-items: center;
      padding: 5px;
  }

  .navbar label {
      display: inline-flex;
      align-items: center;
      cursor: pointer;
  }
  
  #toggler:checked ~ .menu {
      max-height: 100%;
  }
}

@media screen and (min-width: 600px) {
  .LinkNav {
    font-size: 15px;
  }
}

@media screen and (min-width: 800px) {
  .LinkNav {
    font-size: 16px;
  }
}

@media screen and (min-width: 1200px) {
  h1 {
    font-size: 22px;
  }
}

