/* TARIFS */
/* TARIFS */
/* TARIFS */

.card {
  width: 80%;
  margin-left: 10%;
  color: white;
  margin-top: 20px;
  margin-bottom: 20px;
  border-radius: 10px;
  background: #363636;
}

.card-top {
  display: grid;
  grid-template-columns: [a] 1fr [b] 1fr [c];
  grid-template-rows: [a] 1fr [b] 1fr [c];
}

.picture-tarifs {
  width: 100%;
  grid-row: a / c;
  grid-column: a / c;
  border-radius: 10px 10px 0 0;
}

.title-card {
  text-align: center;
  padding: 10px;
  grid-row: a / c;
  grid-column: a / c;
  align-self: center;
  text-transform: uppercase;
  font-size: 30px;
  text-shadow: 0 0 15px black;
}

.separator-card {
  width: 80%;
  margin-left: 10%;
  margin-top: 10px;
  margin-bottom: 10px;
}

.card > p {
  padding: 20px;
}

.span-card {
  color: gray;
}

.price {
  color: green;
  text-align: center;
  padding: 10px;
  font-size: 30px;
  text-transform: uppercase;
}

@media screen and (min-width: 600px) {
  .container-card {
    display: grid;
    grid-template-columns: [] 1fr [] 1fr [];
  }
}

@media screen and (min-width: 1200px) {
  .container-card {
    display: grid;
    grid-template-columns: [] 1fr [] 1fr [] 1fr [];
  }
}

@media screen and (min-width: 1600px) {
  .container-card {
    display: grid;
    grid-template-columns: [] 1fr [] 1fr [] 1fr [] 1fr [];
  }
}

