/* CONTACT */
/* CONTACT */
/* CONTACT */

.contact-page {
  width: 100%;
  height: 100vh;
}

.form {
  margin-top: 100px;
  width: 80%;
  margin-left: 5%;
  display: grid;
  justify-items: center;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 12px 28px 0px, rgba(0, 0, 0, 0.1) 0px 2px 4px 0px, rgba(255, 255, 255, 0.05) 0px 0px 0px 1px inset;
  border: 0,1px solid black;
  padding: 20px;
  justify-self: center;
}

.bloc-form {
  width: 100%;
  margin: 0;
  display: grid;
  justify-items: center;
}

.bloc-form > input {
  padding: 5px;
  margin-top: 5px;
  width: 90%;
  font-size: 16px;
}

.btn {
  width: 93%;
  padding: 10px;
  margin-top: 5px;
  border-radius: 4px;
  border: 1px solid white;
  background-color: rgb(28, 98, 190);
  color: white;
  text-transform: uppercase;
  transition: 500ms;
}

.btn:hover {
  background-color: white;
  color: rgb(28, 98, 190);
  transition: 500ms;
  border: 1px solid rgb(28, 98, 190);
}

.contact-text {
  text-align: center;
  color: white;
  margin-top: 25px;
}

.contact-text > p {
  padding: 5px;
}

@media screen and (min-width: 500px) {
  .form {
    width: 460px;
    margin-left: auto;
    margin-right: auto;
  }
}

