.footer {
  text-transform: uppercase;
  height: 120px;
  background-color: black;
  display: grid;
  grid-template-columns: [a] 1fr [b] 1fr [c];
  grid-template-rows: [a] 1fr [b] 1fr [c];
  align-items: center;
}

.footer-fixed {
  text-transform: uppercase;
  height: 120px;
  background-color: black;
  display: grid;
  grid-template-columns: [a] 1fr [b] 1fr [c];
  grid-template-rows: [a] 1fr [b] 1fr [c];
  align-items: center;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}

.insta {
  grid-row: a / b;
  justify-self: center;
  grid-column: a / c;
  margin-right: 100px;
}

.fb {
  grid-row: a / b;
  justify-self: center;
  grid-column: a / c;
  margin-right: 50px;
}

.linkedin {
